.backdrop {
  height: 100vh;
  width: 100vw;
  background-color: rgba(0, 0, 0, 0.8);
  position: fixed;
  top: 0;
  left: 0;
  z-index: 20;
}

.overlay {
  height: 100vh;
  width: 100vw;
  background-color: rgba(250, 250, 250, 0.8);
  position: fixed;
  top: 0;
  left: 0;
  z-index: 150;
}
